    display: flex;
    margin: 0 0 0 calc(64 / 16)rem;
    width: 100%;
  }
  .footer__link a {
    font-size: calc(13 / 16)rem;
    transition: color 0.3s ease;
  }
  .footer__link a:hover {
    color: #fa5757;
  }
  .footer__link:nth-of-type(2) {
    margin: 0;
  }
  .footer__link:not(:first-of-type) {
    margin-left: calc(48 / 16)rem;
  }
  .footer__social {
    margin: 0 0 0 auto;
  }
  .footer__social path {
    transition: fill 0.3s ease;
  }
  .footer__social svg:hover path {
    fill: #fa5757;
  }
}